import React from 'react';
import { connectEvaluation } from '../common/redux';
import { CompNumber } from './CompNumber';
import { IOperand, CompOperand } from './CompOperand';

interface ICompMultiplicationProps extends React.SVGProps<SVGSVGElement> {
  operands: number[];
}

const FC: React.FC<ICompMultiplicationProps> = ({ id, operands }) => {
  const summandFirst = React.useRef<HTMLDivElement>(null);
  const summandSecond = React.useRef<HTMLDivElement>(null);
  const sum = React.useRef<HTMLDivElement>(null);
  const [operandFirst, operandSecond] = operands;

  const [factors, product] = React.useMemo(() => {
    const operandize = (value: string, maxdigit: number, trail: number) => {
      const ndigit = Math.max(value.length - trail, maxdigit);
      return {
        value: (' '.repeat(ndigit) + value).slice(-ndigit) + '0'.repeat(trail),
        text: ' '.repeat(ndigit) + '0'.repeat(trail),
        mask: 'x'.repeat(ndigit) + 'z'.repeat(trail),
      };
    };
    const factors: IOperand[] = [...String(operandFirst)].map<IOperand>((_, i) => {
      const value = String(operandSecond * ~~((operandFirst / 10 ** i) % 10));
      const ndigit = Math.max(value.length - i, 2);
      return operandize(value, ndigit, i);
    });
    return [factors, operandize(String(operandFirst * operandSecond), 3, 0)];
  }, []);

  return (
    <div className="calculation" style={{ background: 'transparent' }}>
      <CompNumber value={String(operandFirst)} />
      <div className="operator">&#x00d7;</div>
      <CompNumber value={String(operandSecond)} />
      <div className="horizontal-line">==</div>
      {factors.map((factor: IOperand, i: number) => (
        <CompOperand key={i} id={Number(id) + i} operand={factor} />
      ))}
      <div className="horizontal-line">==</div>
      <CompOperand id={Number(id) + 2} operand={product} />
    </div>
  );
};

export const CompMultiplication = connectEvaluation(FC);
