import JSZip from 'jszip';
import { grpc } from '@improbable-eng/grpc-web';
import { asset } from '../proto/asset_pb_service';
import { AssetRequest } from '../proto/asset_pb';

export namespace Asset {
  const HOST = 'https://bacti.tk:8080';
  const URL = window.URL || window.webkitURL;
  const TYPES: any = {
    svg: 'image/svg+xml',
  };
  const POOL: any = {};

  export const fetch = async (uri: string): Promise<string | null> => {
    const [_, extention] = uri.match(/\.(\w+)$/i) || [];
    const type = TYPES[extention];

    if (POOL[uri]) {
      return POOL[uri];
    }
    const request = new AssetRequest();
    request.setName(uri);

    const data: Uint8Array = await new Promise((resolve) =>
      grpc.unary(asset.fetch, {
        request,
        host: HOST,
        onEnd: ({ message, status }: any) => resolve(status ? null : message.getData()),
      })
    );
    if (data == null) return null;

    const zip = await JSZip.loadAsync(data);
    const buffer: any = await zip.file(uri)?.async('arraybuffer');
    POOL[uri] = URL.createObjectURL(new Blob([buffer], { type }));
    return POOL[uri];
  };
}
