import React from 'react';
import { connectLetter, connectEvaluation } from '../common/redux';
import { Util } from '../common/utils';
import { CompLetter } from './CompLetter';

interface ICompAlphabetProps extends React.SVGProps<SVGSVGElement> {
  finished: boolean;
  panel: any;
  pickPanel: any;
  updateScore: any;
  onDragEnd?: any;
}

const FC: React.FC<ICompAlphabetProps> = ({
  finished,
  panel,
  pickPanel,
  updateScore,
  onDragEnd = (event: any) => {
    if (panel) {
      panel.innerHTML = '';
      panel.appendChild(Util.cloneLetter(event.target, panel.valid, updateScore));
    }
  },
}) => {
  return (
    <div style={{ display: 'flex', flexFlow: 'wrap', position: 'fixed', backgroundColor: 'transparent', zIndex: 999 }}>
      {[...'ABCDEFGHIJKLMNOPQRSTUVWXYZ'].map((i) => (
        <CompLetter
          key={i}
          letter={i}
          draggable={!finished}
          onDragStart={(event: any) => pickPanel(null)}
          onDragEnd={onDragEnd}
        />
      ))}
    </div>
  );
};

export const CompAlphabet = connectEvaluation(connectLetter(FC));
