import React from 'react';
import { connectDigit, connectEvaluation } from '../common/redux';
import { CompDigit } from './CompDigit';
import { CompDigitPanel } from './CompDigitPanel';

export interface IOperand {
  value: string;
  text: string;
  mask: string;
}

interface ICompOperandProps extends React.SVGProps<SVGSVGElement> {
  finished: boolean;
  operand: any;
  selectPanel: any;
  updateResult: any;
}

const FC: React.FC<ICompOperandProps> = ({ id, finished, operand, selectPanel, updateResult }) => {
  const [_, refresh] = React.useState(0);
  const number = React.useRef<HTMLDivElement>(null);
  const { value, text, mask } = operand;
  const panel = (digit: string, i: number) => {
    if (isFinite(+digit)) {
      return <CompDigit key={i} digit={+digit} draggable={false} />;
    }
    if (digit == 'z') {
      return <div key={i} className="digit-panel" style={{ borderColor: 'black' }} />;
    }
    if (text[i] == ' ') {
      return (
        <CompDigitPanel
          key={i}
          onDragOver={(event: any) => event.preventDefault()}
          onDrop={(event: any) =>
            selectPanel({
              operand,
              index: i,
            })
          }
        />
      );
    }
    const valueText = (' '.repeat(text.length) + String(value)).slice(-text.length);
    return (
      <CompDigit
        key={i}
        digit={+text[i]}
        style={{
          background: valueText[i] === text[i] ? 'green' : 'red',
        }}
        draggable={!finished}
        onDragEnd={(event: any) => {
          operand.text = text.replace(new RegExp(`(.{${i}})(.)`, 'i'), '$1 ');
          refresh(Math.random());
        }}
      />
    );
  };

  React.useEffect(() => {
    (async () => updateResult(id, value === text))();
  });

  return (
    <div ref={number} style={{ display: 'flex', justifyContent: 'flex-end' }}>
      {[...mask].map(panel)}
    </div>
  );
};

export const CompOperand = connectEvaluation(connectDigit(FC));
