import { connect } from 'redux-zero/react';
import { Util } from '../common/utils';
import { IMAGES } from '../common/defines';

export const connectDigit = connect(
  ({ panel, digitPanel }: any) => ({ panel, digitPanel }),
  (store: any, props: any) => ({
    selectPanel: (state: any, panel: any) => store.setState({ panel }),
    targetPanel: (state: any, digitPanel: any) => {
      store.setState({ digitPanel });
    },
  })
);

export const connectLetter = connect(
  ({ panel }: any) => ({ panel }),
  (store: any, props: any) => ({
    pickPanel: (state: any, panel: any) => {
      store.setState({ panel });
    },
  })
);

export const connectEvaluation = connect(
  ({ session, random, results, penalty, timer, finished }: any) => ({
    session,
    random,
    results,
    penalty,
    timer,
    finished,
  }),
  (store: any, props: any) => ({
    newSession: (state: any) =>
      store.setState({
        session: Math.random(),
        penalty: 0,
        finished: false,
        random: Util.shuffleGenerator(IMAGES.length),
      }),
    updateTimer: (state: any) => store.setState({ timer: Date.now() }),
    updateResult: ({ results }: any, index: number, value: boolean) => {
      results[index] = value;
      store.setState({ results });
    },
    updateScore: ({ penalty }: any, increment: number) => {
      store.setState({ penalty: penalty + increment });
    },
    submit: ({ penalty }: any, increment: number) => {
      store.setState({ penalty: penalty + increment, finished: true });
    },
  })
);
