import React from 'react';
import { Asset } from '../common/asset';
import { IMAGES } from '../common/defines';
import { connectEvaluation } from '../common/redux';

interface ICompDigitPanelProps extends React.SVGProps<SVGSVGElement> {
  session: number;
  random: any;
  onDragOver: any;
  onDrop: any;
}

const FC: React.FC<ICompDigitPanelProps> = ({ session, random, onDragOver, onDrop, ...props }) => {
  const [asset, setAsset] = React.useState<string | null>(null);
  React.useMemo(() => {
    (async () => setAsset(await Asset.fetch(`images/${IMAGES[random.next().value]}`)))();
  }, [session]);

  return (
    <div onDragOver={onDragOver} onDrop={onDrop}>
      <div
        className="digit-panel"
        style={{
          backgroundImage: `url(${asset})`,
        }}
      />
    </div>
  );
};

export const CompDigitPanel = connectEvaluation(FC);
