import React from 'react';
import { connectEvaluation } from '../common/redux';
import { CompNumber } from './CompNumber';
import { IOperand, CompOperand } from './CompOperand';

interface ICompAdditionProps extends React.SVGProps<SVGSVGElement> {
  operands: number[];
}

const FC: React.FC<ICompAdditionProps> = ({ id, operands }) => {
  const [operandFirst, operandSecond] = operands;
  const [operator, result] = React.useMemo(() => {
    const operandize = (value: string, maxdigit: number, trail: number) => {
      const ndigit = Math.max(value.length - trail, maxdigit);
      return {
        value: (' '.repeat(ndigit) + value).slice(-ndigit) + '0'.repeat(trail),
        text: ' '.repeat(ndigit) + '0'.repeat(trail),
        mask: 'x'.repeat(ndigit) + 'z'.repeat(trail),
      };
    };

    const operator = operandFirst > operandSecond ? '-' : '+';
    const result = {
      '+': operandFirst + operandSecond,
      '-': operandFirst - operandSecond,
    }[operator];
    return [operator, operandize(String(result), 3, 0)];
  }, []);

  return (
    <div className="calculation" style={{ background: 'transparent' }}>
      <CompNumber value={String(operandFirst)} />
      <div className="operator">{operator}</div>
      <CompNumber value={String(operandSecond)} />
      <div className="horizontal-line">==</div>
      <CompOperand id={id} operand={result} />
    </div>
  );
};

export const CompAddition = connectEvaluation(FC);
