import React, { SVGProps, useState, useEffect } from 'react';
import { connect } from 'redux-zero/react';
import Draggable from 'react-draggable';
import { IPageProps } from '.';
import { CompDigit } from '../components/CompDigit';
import { CompEvaluation } from '../components/CompEvaluation';
import { CompAddition } from '../components/CompAddition';
import { CompMultiplication } from '../components/CompMultiplication';
import { connectDigit, connectEvaluation } from '../common/redux';
import { Util } from '../common/utils';
import { SIZE, MIN, MAX, MULTIPLY } from '../common/defines';

interface ICalculationProps extends IPageProps {
  finished: boolean;
  session: number;
  panel: any;
  selectPanel: any;
  updateScore: any;
  updateTimer: any;
}

const FC: React.FC<ICalculationProps> = (props) => {
  const [_, refresh] = React.useState(0);
  const { finished, session, panel, selectPanel, updateScore, updateTimer } = props;
  const problems = React.useMemo(() => {
    let offset = 0;
    return [...Array(SIZE)].map((_, i) => {
      const multiply = i < MULTIPLY;
      const problem = {
        operands: [Util.random(MIN, MAX), multiply ? Util.random(2, 10) : Util.random(MIN, MAX)],
        multiply,
        offset,
      };
      offset += ~~multiply * 2 + 1;
      return problem;
    });
  }, [session]);

  React.useEffect(() => {
    updateTimer();
  }, [session]);

  return (
    <div key={session}>
      <div style={{ display: 'flex', position: 'fixed', backgroundColor: 'transparent', zIndex: 999 }}>
        {[...Array(10)].map((_, i) => (
          <CompDigit
            key={i}
            digit={i}
            draggable={!finished}
            onDragStart={(event: any) => selectPanel(null)}
            onDragEnd={(event: any) => {
              if (panel) {
                const { index, operand } = panel;
                operand.text = operand.text.replace(new RegExp(`(.{${index}})(.)`, 'i'), `$1${event.target.innerText}`);
                updateScore(~~(operand.value[index] === operand.text[index]) - 1);
                refresh(Math.random());
              }
            }}
          />
        ))}
      </div>
      <CompEvaluation />
      <div className="content-panel">
        <div style={{ display: 'flex', flexFlow: 'wrap', backgroundColor: 'linen' }}>
          {problems.map(({ operands, multiply, offset }, i) => {
            return multiply ? (
              <CompMultiplication key={i} id={offset} operands={operands} />
            ) : (
              <CompAddition key={i} id={offset} operands={operands} />
            );
          })}
        </div>
      </div>
    </div>
  );
};

export const PageCalculation = connectEvaluation(connectDigit(FC));
