import amplitude from 'amplitude-js';
import uuid from 'uuid';

export namespace Tracking {
  export enum ETypes {
    IMPRESSION = 'IMPRESSION',
    ENGAGEMENT = 'ENGAGEMENT',
    SUBMIT = 'SUBMIT',
    REFRESH = 'REFRESH',
  }

  interface ITrackingEvent {
    event: string;
    loading?: number;
    playing?: number;
    score?: number;
  }

  const API_KEY = 'e9d181173175fa90e6140967d58d18e0';
  const client = amplitude.getInstance();
  client.init(API_KEY);

  const storage: any = window.localStorage || {};
  storage.anoId = storage.anoId || uuid.v4();
  client.setUserId(`ano:${storage.anoId}`);

  export const logEvent = ({ event, ...data }: ITrackingEvent) => {
    const user = window.localStorage.getItem('username');
    client.logEvent(event, { ...data, user });
  };
}
