import React from 'react';

interface ICompDigitProps extends React.SVGProps<SVGSVGElement> {
  digit: number;
  draggable: boolean;
  onDragStart?: any;
  onDragEnd?: any;
}

export const CompDigit: React.FC<ICompDigitProps> = (props) => {
  const { digit, draggable, onDragStart, onDragEnd, style } = props;
  return (
    <div draggable={draggable} onDragStart={onDragStart} onDragEnd={onDragEnd} style={style} className="digit">
      {digit}
    </div>
  );
};
