import React from 'react';
import { Page, IPageProps } from '.';
import { connectDigit, connectEvaluation } from '../common/redux';
import { Asset } from '../common/asset';
import { Tracking } from '../common/tracking';

const FC: React.FC<IPageProps> = ({ push }) => {
  React.useMemo(() => {
    const t = Date.now();
    Tracking.logEvent({ event: Tracking.ETypes.IMPRESSION });
    (async () => {
      const FONTS = [
        {
          family: 'Digit-Font',
          source: 'fonts/HP001_5_hang_bold.ttf',
        },
        {
          family: 'Letter-Font',
          source: 'fonts/FineCollege.ttf',
        },
        {
          family: 'SaucerBB',
          source: 'fonts/SaucerBB.ttf',
        },
      ];

      Promise.all(
        FONTS.map(async ({ family, source }: any) => {
          // @ts-ignore
          const font = new FontFace(family, `url(${await Asset.fetch(source)})`);
          await font.load();
          // @ts-ignore
          document.fonts.add(font);
        })
      )
        .then(() => {
          Tracking.logEvent({ event: Tracking.ETypes.ENGAGEMENT, loading: Date.now() - t });
          push(Page.ETypes.PROFILE);
        })
        .catch(() => null);
    })();
  }, []);

  return (
    <div
      style={{
        width: '100vw',
        height: '100vh',
        backgroundColor: '#000000',
      }}
    >
      <div
        className="loader"
        // className="lds-dual-ring"
      />
    </div>
  );
};

export const PageLoading = connectEvaluation(connectDigit(FC));
