import React from 'react';
import createStore from 'redux-zero';
import { Provider, connect } from 'redux-zero/react';
import { IMAGES } from '../common/defines';
import { Util } from '../common/utils';
import { PageLoading } from './PageLoading';
import { PageProfile } from './PageProfile';
import { PageCalculation } from './PageCalculation';
import { PageEnglish } from './PageEnglish';
import './index.scss';
import './fonts.scss';

export interface IPageProps extends React.SVGProps<SVGSVGElement> {
  push: any;
  pop: any;
}

export namespace Page {
  export enum ETypes {
    LOADING,
    PROFILE,
    CALCULATION,
    ENGLISH,
  }

  const pages = (props: any) => ({
    [ETypes.LOADING]: <PageLoading key="PageLoading" {...props} />,
    [ETypes.PROFILE]: <PageProfile key="PageProfile" {...props} />,
    [ETypes.CALCULATION]: <PageCalculation key="PageCalculation" {...props} />,
    [ETypes.ENGLISH]: <PageEnglish key="PageEnglish" {...props} />,
  });
  const stack: ETypes[] = [];
  const store = createStore({
    currentPage: ETypes.LOADING,
    results: [],
    penalty: 0,
    finished: false,
    random: Util.shuffleGenerator(IMAGES.length),
  });

  const mapToProps = ({ currentPage }: any) => ({ currentPage });
  const actions = (store: any, props: any) => ({
    push: ({ currentPage }: any, next: ETypes) => {
      stack.push(currentPage);
      store.setState({ currentPage: next });
    },
    pop: (state: any) => {
      const previous = stack.pop();
      store.setState({ currentPage: previous });
    },
  });
  const Content = connect(mapToProps, actions)((props: any) => pages(props)[props.currentPage as ETypes]);

  export const Display: any = () => (
    <Provider store={store}>
      <Content />
    </Provider>
  );
}
