import React from 'react';
import { Page, IPageProps } from '.';
import { CompLetter } from '../components/CompLetter';
import { CompAlphabet } from '../components/CompAlphabet';
import { CompLetterPanel } from '../components/CompLetterPanel';
import { connectLetter } from '../common/redux';

interface IPageProfileProps extends IPageProps {
  panel: any;
  pickPanel: any;
}

const FC: React.FC<IPageProfileProps> = ({ panel, push, pickPanel }) => {
  const [username, setUsername] = React.useState('   ');
  const currentUser = window.localStorage.getItem('username');
  const formatter = (text: string, letter: string, index: number) => {
    const letters = [...text];
    letters[index] = letter;
    return `  ${letters.join('').replace(/(\s+)/g, ' ').trim()}  `;
  };

  React.useEffect(() => {
    if (currentUser) {
      push(Page.ETypes.CALCULATION);
    }
  }, []);

  if (currentUser) return null;
  return (
    <div>
      <CompAlphabet
        onDragEnd={(event: any) => {
          if (!panel) return;
          setUsername(formatter(username, event.target.innerText, panel.index));
        }}
      />
      <div className="word">
        <div className="word-panel" style={{ paddingTop: 100 }}>
          {[...username].map((letter, i) =>
            letter == ' ' ? (
              <CompLetterPanel
                key={i}
                onDragOver={(event: any) => event.preventDefault()}
                onDrop={(event: any) => {
                  const panel = event.currentTarget;
                  panel.index = i;
                  pickPanel(panel);
                }}
              />
            ) : (
              <CompLetter
                key={i}
                letter={letter}
                draggable={true}
                onDragEnd={(event: any) => {
                  setUsername(formatter(username, ' ', i));
                }}
              />
            )
          )}
        </div>
        <div
          style={{
            fontSize: 'x-large',
            fontWeight: 'bold',
            backgroundColor: 'yellow',
            margin: 40,
            padding: '16px 53px',
            border: '4px solid blue',
            cursor: 'pointer',
            pointerEvents: 'auto',
          }}
          onClick={() => {
            window.localStorage.setItem('username', username.toLowerCase().trim());
            push(Page.ETypes.CALCULATION);
          }}
        >
          SAVE
        </div>
      </div>
    </div>
  );
};

export const PageProfile = connectLetter(FC);
