import React from 'react';
import { connectEvaluation } from '../common/redux';
import { Asset } from '../common/asset';
import { Tracking } from '../common/tracking';

interface ICompEvaluationProps extends React.SVGProps<SVGSVGElement> {
  finished: boolean;
  results: any;
  timer: any;
  penalty: any;
  newSession: any;
  submit: any;
}

const FC: React.FC<ICompEvaluationProps> = (props) => {
  const { results, timer, penalty, newSession, finished, submit } = props;
  const [asset, setAsset] = React.useState<string | null>(null);
  const username = React.useMemo(() => {
    (async () => setAsset(await Asset.fetch('reset.png')))();
    return window.localStorage.getItem('username');
  }, []);

  const evaluation = () =>
    Math.ceil(
      results.reduce((mark: number, result: boolean) => {
        return mark + (100 / results.length) * ~~result;
      }, 0)
    );

  return (
    <div
      style={{
        position: 'fixed',
        top: 200,
        right: 0,
        width: 180,
        paddingRight: 40,
        textAlign: 'center',
        zIndex: 999,
      }}
    >
      <div className="user">{username}</div>
      <div
        style={{
          fontWeight: 'bold',
          margin: 16,
          cursor: 'pointer',
          height: 96,
          backgroundImage: `url(${asset})`,
          backgroundSize: 'contain',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center',
        }}
        onClick={() => {
          Tracking.logEvent({
            event: Tracking.ETypes.REFRESH,
            score: evaluation(),
            playing: Date.now() - timer,
          });
          newSession();
        }}
      />

      <div
        style={{
          fontFamily: 'SaucerBB',
          fontSize: 80,
          color: finished ? 'red' : 'grey',
        }}
      >
        {100 + penalty}
      </div>
      <div
        style={{
          fontWeight: 'bold',
          backgroundColor: 'yellow',
          padding: 4,
          border: '2px solid blue',
          cursor: 'pointer',
          pointerEvents: finished ? 'none' : 'auto',
        }}
        onClick={() => {
          const score = evaluation();
          Tracking.logEvent({ event: Tracking.ETypes.SUBMIT, score: score + penalty, playing: Date.now() - timer });
          submit(score - 100);
        }}
      >
        SUBMIT
      </div>
    </div>
  );
};

export const CompEvaluation = connectEvaluation(FC);
